import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Jobs from '../views/Jobs.vue'
import Contact from '../views/Contact.vue'
import Skills from '../views/Skills.vue'
import Portafolios from '../views/Portafolios.vue'
import GalleryView from '../views/GalleryView.vue'
import CaquiView from '../views/CaquiView.vue'

import AzucenaView from '../views/AzucenaView.vue'

import NotFound from '../views/NotFound.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/jobs',
    name: 'jobs',
    component: Jobs
  },
  {
    path: '/skills',
    name: 'skills',
    component: Skills
  },
  {
    path: '/portafolios',
    name: 'portafolios',
    component: Portafolios
  },
  {
    path: '/contact',
    name: 'contact',
    component: Contact
  },
  {
    path: '/gallery',
    name: 'gallery',
    component: GalleryView
  },
  {
    path: '/caqui',
    name: 'caqui',
    component: CaquiView
  },
  {
    path: '/azucena',
    name: 'azucena',
    component: AzucenaView
  },
  {
    path: '*',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
